.header {
  background-color: #0266C1;
  padding: 32px 20px;
  max-width: 1200px;
  margin: auto;

  @media (max-width: $tablet) {
    padding: 20px 45px;
  }
}

.header__container {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.header__contacts {
  @include semibold;
  padding: 30px 0;
  color: #FFFFFF;
}
